import React, { Component } from 'react';
import Button from '../../../components/UI/Button/Button';
import classes from './ContactData.module.css';
import axios from '../../../axios-orders';
import Spinner from '../../../components/UI/Spinner/Spinner';
import Input from '../../../components/UI/Input/Input';
import order from '../../../components/orders/Order';
import {connect} from 'react-redux'
import * as actions from '../../../store/actions/index'

class ContactData extends Component{

    state={
       orderForm:{

        name:{
            elementType:"input",
            elementConfig:{
                type:'text',
                placeholder:'Your Name'
            },
            value:'',
            validation:{
                required:true
            },
            valid:false,
            touched:false
        },
        street:{
            elementType:"input",
            elementConfig:{
                type:'text',
                placeholder:'Your Address'
            },
            value:'',
            validation:{
                required:true
            },
            valid:false,
            touched:false
        },
        pincode:{
            elementType:"input",
            elementConfig:{
                type:'text',
                placeholder:'zipcode'
            },
            value:'',
            validation:{
                required:true,
                minLength:5,
                maxLength:5
            },
            valid:false,
            touched:false
        },
        email:{
            elementType:"input",
            elementConfig:{
                type:'text',
                placeholder:'Email'
            },
            value:'',
            validation:{
                required:true
            },
            valid:false,
            touched:false
        },

        deliveryMode:{
            elementType:"select",
            elementConfig:{
               options:[{value:'',displayValue:'--Please select delivery speed--'},{value:'fastest',displayValue:'Fastest'},{value:'cheapest',displayValue:'Cheapest'}]
            },
            value:'fastest',
            valid:true,
            validation:{}
            
        }


       },
       formValid:false
    }


    orderHandler=(event)=>{
        event.preventDefault()
       
         this.setState({
            loading:true
        })

        const formData={};

        for(let key in this.state.orderForm){

            formData[key]=this.state.orderForm[key].value
        }
        const orders={
            ingredients:this.props.ings,
            orderData:formData,
            price:this.props.price,
            userId:this.props.userId    
        };

        this.props.onOrderBurger(orders,this.props.token);

       
  

    }

    checkValidity(value,rules){
        let isValid=true;

        if(rules.required){
            isValid=value.trim() !=='' && isValid ;
        }
        
        if(rules.minLength){
            isValid=value.trim().length >= rules.minLength  && isValid;
        }
        

        if(rules.maxLength){
            isValid=value.trim().length <= rules.maxLength  && isValid;
        }
       

        return isValid;
    }

    inputChangeHandler=(event,inputIdentifier)=>{

        // console.log(event.target.value)
        const updatedOrderForm={
            ...this.state.orderForm
        }

        const updatedFormElement={...updatedOrderForm[inputIdentifier]};
        updatedFormElement.value=event.target.value;
        updatedFormElement.touched=true;
        updatedFormElement.valid=this.checkValidity(updatedFormElement.value,updatedFormElement.validation)
        updatedOrderForm[inputIdentifier]=updatedFormElement;
        console.log(updatedFormElement);

        let formIsValid=true;
        for(let inputIdentifier in updatedOrderForm){
            formIsValid=updatedOrderForm[inputIdentifier].valid && formIsValid;
        }

        this.setState({orderForm:updatedOrderForm,formValid:formIsValid})

    }

    render(){

        const formElementsArray=[];
        for(let key in this.state.orderForm){

            formElementsArray.push({
                id:key,
                config:this.state.orderForm[key]
            })
        }

        let form=(
        <form onSubmit={this.orderHandler}>
        {/* <Input elementType="..." elementConfig="..." value="..."  ></Input> */}
            {formElementsArray.map(formElement=>{
                return <Input key={formElement.id} elementType={formElement.config.elementType}
                        elementConfig={formElement.config.elementConfig}
                        value={formElement.config.value}
                        changed={(event)=>this.inputChangeHandler(event,formElement.id)}
                        invalid={!formElement.config.valid}
                        shouldValidate={formElement.config.validation}
                        touched={formElement.config.touched}></Input>
            })}
        <Button btnType="Success" disabled={!this.state.formValid}  >Order</Button>
    </form>)

        if(this.props.loading){
            form=<Spinner/>
        }
        
        
        return(
            <div className={classes.ContactData}>
                <h4>Enter contact details</h4>
               {form}
            </div>
        )
    }
}

const mapStateToProps=(state)=>{
    return{

        ings:state.burgerBuilder.ingredients,
        price:state.burgerBuilder.totalPrice,
        loading:state.order.loading,
        token:state.auth.token,
        userId:state.auth.userId

    }
}

const mapDispatchToProps=dispatch=>{
    
    return{
        onOrderBurger:(orderData,token)=>dispatch(actions.purchaseBurger(orderData,token))
    }
}

export default connect(mapStateToProps,mapDispatchToProps)(ContactData);


