import React,{Component} from 'react';
import {Route,Redirect} from 'react-router-dom';

import CheckoutSummary from '../../components/orders/CheckoutSummary/CheckoutSummary';
import ContactData from '../../containers/Checkout/ContactData/ContactData';
import {connect} from 'react-redux'
import * as actionCreators from '../../store/actions/index';



class Checkout extends Component{


    state={
        ingredients:null,
        totalPrice:null
    }

    // componentWillMount(){
    //     this.props.onPurchaseInit()
    // }
    // componentWillMount(){

       

    //     const query=new URLSearchParams(this.props.location.search);
    //     let price=0;
    //     const ingredient={

    //     }
    //     // console.log(query.entries().next());
    //     for(let i of query.entries()){
    //         console.log(i);
    //         if(i[0]==='price'){
    //             price   =i[1]
    //         }
    //         else {
    //             ingredient[i[0]]=+i[1];
    //         }
            
    //     }


    //     this.setState({ingredients:ingredient,totalPrice:price})
    //     // console.log(query.entries().next());
    // }

    checkoutCancelledHandler=()=>{

        this.props.history.goBack()
    }



    checkoutContinuedHandler=()=>{
        this.props.history.push('/checkout/contact-data')
    }

    render()
    {

        let checkoutSummary=<Redirect to="/"></Redirect>
        if(this.props.ingr){
            let purchaseInit=this.props.purchase ? <Redirect to ="/"></Redirect>:null;
            checkoutSummary=
            <div>
                 {purchaseInit}
                <CheckoutSummary ingredients={this.props.ingr}
                checkoutCancelled={this.checkoutCancelledHandler}
                checkoutContinued={this.checkoutContinuedHandler}></CheckoutSummary> 
                <Route path={this.props.match.path + '/contact-data'} component={ContactData}></Route>
               
            </div>
        }

        console.log(this.props);
        return checkoutSummary
            
        
    }


}


const mapStateToProps=(state)=>{

    return{

        ingr:state.burgerBuilder.ingredients,
        price:state.burgerBuilder.totalPrice,
        purchase:state.order.purchase
    }
}


// const mapDipstachToProps=dispatch=>{
//     return{

//         onPurchaseInit:()=>dispatch(actionCreators.purchaseInit())
//     }
// }

export default connect(mapStateToProps)(Checkout);


